@keyframes spin-coin {
  0% { transform: rotateY(0deg); }
  100% { transform: rotateY(360deg); }
}
.floating-card-logo.spinning-logo {
  animation: spin-coin 0.8s cubic-bezier(.4,2,.6,1);
  backface-visibility: hidden;
  perspective: 600px;
} 

.floating-card-tour-section {
  display: flex;
  justify-content: center;
  margin-top: 16px;
  padding-top: 12px;
  border-top: 1px solid rgba(0,0,0,0.08);
}
.floating-card-tour-btn {
  background: linear-gradient(135deg, #38bdf8 0%, #2563eb 100%);
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.18s cubic-bezier(.4,2,.6,1);
  box-shadow: 0 4px 16px rgba(56,189,248,0.25);
  position: relative;
  overflow: hidden;
}
.floating-card-tour-btn:hover {
  background: linear-gradient(135deg, #2563eb 0%, #38bdf8 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(56,189,248,0.35);
}
.floating-card-tour-btn:active {
  transform: translateY(0);
}
.tour-btn-icon {
  font-size: 1.2rem;
  animation: spin-slow 3s linear infinite;
}
@keyframes spin-slow {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.tour-btn-text {
  font-weight: 600;
  letter-spacing: 0.5px;
} 

/* Estilos para el polgono del centro comercial con efecto blur */
.leaflet-overlay-pane svg path {
  filter: blur(1px);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
}

/* Aplicar blur especfico al polgono del CC */
.leaflet-overlay-pane svg path[fill="#ff0000"] {
  filter: blur(2px) saturate(1.2);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  mix-blend-mode: multiply;
} 